.App {

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.horseButton, .horseButton:focus, .horseButton:active {
  background: transparent !important;
  height: auto !important;
  outline: 0 !important;
  box-shadow: none !important;
  padding: 0 !important;
  margin: 0 !important;
  min-width: 0 !important;
}

div.table {
  display: table;
  width: 100%;
  padding: 0;
  margin: 0;
  border: 0;
  border-spacing: 0;
  border-collapse: collapse;
  line-height: 1;
}

div.table .tr {
  display: table-row;
  padding: 0;
}

div.table .td {
  display: table-cell;;
  padding: 0;
}

div.table .td.right {
  text-align: right;
}

.m-fadeOut {
  background-color: rgba(0,255,0,0.2);
  /*opacity: 0;*/
  transition: linear 0ms, background-color 0ms;
}
.m-fadeIn {
  background-color: transparent;
  /*opacity: 1;*/
  transition: linear 300s, background-color 300ms;
}

.mobile-outer-wrap {
  max-width: 700px;
  xmin-width: 428px;
  margin: auto;
  padding: 6px;
  overflow-x: scroll;
}

.clearFix {
  clear:both;
}

.sticky {
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
}
